import Vue from "vue";
import VueRouter from "vue-router";
import Landing from "../views/Landing.vue";
import FAQ from "../views/FAQ.vue"

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Landing,
  },
  {
    path: "/faq",
    name: "faq",
    component: FAQ,
  },
  
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
